body {
  background-image: url(assets/FondoPrincipal.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%; 
  margin: 0; 
}
.App-header {
  text-align: center;
}

.App-header h1 {
  color: #ffffff; 
  transition: color 0.5s; 
  cursor: pointer; 
}

.App-header h1:hover {
  color: rgb(255, 0, 0); 
  animation: colorChange 2s infinite; 
}

@keyframes colorChange {
  0% {
    color: rgb(255, 0, 0);
  }
  25% {
    color: rgb(0, 255, 0);
  }
  50% {
    color: rgb(0, 0, 255);
  }
  75% {
    color: rgb(255, 255, 0);
  }
  100% {
    color: rgb(255, 0, 0);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}
nav {
  background-color: #282c34;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-icon {
  cursor: pointer;
  font-size: 24px;
  color: white;
}

.menu-items {
  position: absolute;
  top: 60px; 
  left: 0; 
  background-color: #282c34;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start; 
  width: 100%; 
}

.menu-items div {
  color: white;
  cursor: pointer;
  margin-bottom: 10px;
  text-align: left; 
}
@media screen and (min-width: 1200px) {
  .menu-items {
    flex-direction: row; 
  }

  .menu-items div {
    margin-right: 20px; 
  }
}

@media screen and (min-width: 600px) and (max-width: 1199px) {
  .menu-items {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  }

  .menu-items div {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 599px) {
  .menu-items {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  }

  .menu-items div {
    margin-bottom: 10px;
  }
}